.castboxes{
	text-align: center;
	vertical-align: top;
}
.castbox-kind{
	color: white;
	font-size: 3vmax;
}
.castbox{
	background-color: white;
	vertical-align: top;
	width: 250px;
	height: 280px;
	border: solid;
	border-width: 1px;
	border-radius: 10px;
	padding: 10px;
	margin: 20px;
}
.castbox-name{
	font-size: 30px;
}
.castbox-img{
	width: 100%;
}
.castbox-description{
	font-size: 16px;
}
.modal-main{
	height: 100%;
	color: white;
}
.modal-name{
	text-align: center;
	font-size: 30px;
}
@media screen and (min-width: 750px){
	.modal-left{
		display: inline-block;
		width: 40%;
		text-align: center;
	}
	.modal-right{
		display: inline-block;
		vertical-align: top;
		padding-left: 10px;
		width: calc(60% - 10px);
	}
	.modal-description{
		padding-left: 10px;
	}
}
@media screen and (max-width: 750px){
	.modal-left{
		text-align: center;
		font-size: 5vmin;
		width: 100%;
	}
	.modal-right{
		vertical-align: top;
		width: 100%;
	}
}
.modal-img{
	width: 80%;
}
.modal-combined{
	width: 100%;
}
.modal-description{
	display: inline-block;
	vertical-align: top;
}
.modal-description p{
	margin: 0;
}